@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
:root{--color-primary: #FFE5D4;--color-secondary: #B2CFE6;--color-tertiary: #BCCCE0;--color-text: #17223B;--font-size-base: 1rem}*{margin:0;box-sizing:border-box;font-family:'Quicksand', sans-serif}body{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#FFE5D4;background-color:var(--color-primary);color:#17223B;color:var(--color-text);font-size:1rem;font-size:var(--font-size-base)}

.iconWithAction{display:flex;justify-content:center;align-items:center;width:10rem;height:10rem;border-radius:10px;background-color:#fff;margin:3rem}.iconWithAction:hover{cursor:pointer}.iconWithAction:hover i{font-size:6rem}.iconWithAction i{color:var(--color-text);font-size:5rem;transition:all 0.15s ease-in-out}.icon{display:flex;justify-content:center;align-items:center;width:10rem;height:10rem;border-radius:10px;background-color:#fff;margin:3rem}.icon i{color:var(--color-text);font-size:5rem;transition:all 0.15s ease-in-out}

.iconSelector{display:grid;justify-items:center;align-items:center;grid-template-columns:1fr 1fr 1fr;margin:5rem}

.header{margin-top:2rem;text-align:center}.header h1{font-size:5rem}

.result{display:grid;grid-template-columns:1fr;justify-items:center;align-items:center}.result__question{display:grid;justify-items:center;align-items:center}.result__question h2{font-size:3rem;font-weight:bold}.result__question button{margin-top:1rem;background-color:var(--color-secondary);font-weight:bold;padding:1rem;font-size:1rem;border-radius:10px;cursor:pointer;border:none;transition:all 0.2s ease-in-out}.result__question button:hover{background-color:var(--color-tertiary);font-size:1.3rem;font-style:italic}.result__icons{display:grid;grid-template-columns:1fr 1fr}

.homePage{height:80vh;display:grid;justify-items:center;align-items:center}.homePage__starting{display:grid;justify-items:center;align-items:center}.homePage__result{display:grid;justify-items:center;align-items:center}.homePage h2{font-size:2.5rem;font-weight:bold}.hidden{display:none}

